.best-masters{
	background-color: #f9f9f9;
	padding: 120px 0;

	&__title{
		margin-bottom: 117px;
	}

	&__slider{
		.slider{

			&__wrapper{
				position: relative;
			}

			&__item{
				margin-right: 33px;
				width: 33%;
				background-color: #fff;
				box-shadow: 0 0 12px #f2f2f2;
				padding-bottom: 30px;

				&:last-child{
					margin-right: 0;
				}
			}

			&__avatar{
				height: 257px;
				margin-bottom: 27px;
				position: relative;
			}

			&__block{
				margin: 0 29px;
			}

			&__content{;
				border-bottom: 1px solid #c5c5c5;
				padding-bottom: 17px;
				margin-bottom: 16px;

				.content{
					&__position{
						font-weight: 300;
						font-size: 16px;
					}
				}
			}
		}
	}

	&__popular{
		background-color: #f7a238;
		display: inline-flex;
		align-items: center;
		padding: 4px 9px;
		width: 140px;
		transform: rotate(86deg) skew(-3deg);

		position: absolute;
		top: 48px;
		right: -39px;
		z-index: 2;

		&-text{
			font-size: 14px;
			color: #fff;
			text-transform: uppercase;
			font-weight: 900;
		}
	}
}

@media screen and (max-width: 1000px){
	.best-masters{
		&__slider{
			.slider{
				&__list{
					overflow: hidden;
				}

				&__item{
					min-width: 48%;
				}

				&__controlls{
					position: absolute;
					z-index: 2;
					top: 50%;
					left: 0;
					transform: translateY(-50%);
					height: 40px;
					width: 40px;
					min-width: inherit;

					&--next{
						right: 0;
						left: inherit;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 540px){
	.best-masters{
		&__title.title{
			line-height:28px;
		}

		&__slider{
			.slider{
				&__item{
					min-width: 100%;
				}
			}
		}
	}
}