.intro{
	width: 100%;
	background: url(../images/intro-bg.png) center center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	
	height: 1px;
	min-height: 700px;

	&__wrapper{
		height: 100%;
		width: 100%;
		background-color: rgba(#000000, .6);
	}

	&__content{
		margin-top: 90px;
		display: inline-block;
    	width: 76%;
	}

	&__title{
		color: #fff;
		font-weight: 900;
		font-size: 48px;
		line-height: 58px;
	}

	&__description{
		font-size: 24px;
		font-weight: 100;
		color: #fff;
		margin-top: 27px;
    	line-height: 35px;

		span.pink{
			color: #ff0079;
			font-weight: 900;
		}
	}
}

@media screen and (max-width: 1000px){
	.intro{
		&__content{
			width: 100%;
			text-align: center;
		}

		&__title{
			font-size: 34px;
		}

		&__description{
			font-size: 21px;
		}
	}
}

@media screen and (max-width: 600px){
	.intro{
		&__title{
			font-size: 27px;
		}

		&__description{
			font-size: 17px;
		}
	}
}

@media screen and (max-width: 480px){
	.intro{
		&__title{
			line-height: 48px;
		}

		&__description{
			line-height: 30px;
		}
	}
}

@media screen and (max-width: 350px){
	.intro{
		
		&__content{
			margin-top: 40px;
		}

		&__title{
			line-height: 40px;
			font-size: 23px;
		}

		&__description{
			line-height: 27px;
			font-size: 15px;
			margin-top: 13px;
		}
	}
}