.calculate-the-cost{
	background-color: #fff;
	position: relative;
	z-index:3;
	box-shadow: 0 0 12px #f2f2f2;
	margin-top: 62px;

	.container{
		display: flex;
	}

	&__title{
		margin-bottom: 45px;
	}

	&__col{
		background-color: #fff;
		width: 66%;
    	padding: 62px 70px 60px 70px;

		&--another{
			background-color: #f9f9f9;
			flex: 1;
			padding: 0 48px;
			padding-top: 57px;

			.calculate-the-cost__title.title{
				font-size: 20px;
				line-height: 29px;
				margin-bottom: 39px;
			}

			.calculate-the-cost__row{
				display: flex;
			}

			.calculate-the-cost__icon{
				&:after{
					border:none;
				}

				&--name{
					&:after{
						height: 21px;
					    width: 20px;
					    background: url(../images/icon/name.png);
					    top: 50%;
					    left: 50%;
					    transform: translate(-50%,-50%);
					}
				}

				&--tel{
					&:after{
						height: 16px;
					    width: 15px;
					    background: url(../images/icon/tel.png);
					    top: 50%;
					    left: 50%;
					    transform: translate(-50%,-50%);
					}
				}
			}

			.calculate-the-cost__row{
				margin-bottom: 30px;
			}
		}
	}

	&__row{
		margin-bottom: 54px;

		&:last-child{
			margin-bottom:0;
		}
	}

	&__list{
		display: flex;
		justify-content:space-between;
	}

	&__item{
		flex: 1;
		margin-right: 90px;

		&:last-child{
			margin-right:0;
		}
	}

	&__name{
		color: #909090;
		font-size: 20px;
		font-weight: 900;
		margin-bottom: 28px;
		white-space: nowrap;

		&--o{
			margin-bottom: 21px;
		}
	}

	&__option{
		display: flex;
	}

	&__border{
		min-height: 50px;
		line-height: 50px;
		border: 1px solid #c5c5c5;
		color: #c5c5c5;
		font-weight: 100;
		font-size: 16px;
		width: 100%;
		padding: 0 10px
	}

	&__icon{
		width: 60px;
		border: 1px solid #c5c5c5;
		position: relative;
		cursor: pointer;

		&:after{
			content:'';
			display: inline-block;
			border: 7px solid transparent;
			border-top: 7px solid #ea3180;
			height: 0;
			width: 0;

			position: absolute;
			top: 57%;
			left: 50%;
			transform: translate(-50%,-57%);
		}

		&:hover{
			background-color: #c5c5c5;
		}
	}
	
	&__checkbox{
		display: flex;
		flex-direction: column;
	}

	&__x{
		font-weight: 900;
		font-size: 24px;
		color: #ea3180;
		height: 100%;
		margin: auto 18px;
	}

	&__label{
		display: flex;
		align-items: center;
		margin-bottom: 17px;

		&:last-child{
			margin-bottom: 0;
		}
	}

	&__subtitle{
		font-weight: 100;
		font-size: 20px;
		color: #767676;
	}

	&__check{
		display: none;

		&:checked + .calculate-the-cost__fake:before{
			content:'\2713';
		}
	}

	&__fake{
		height: 15px;
		width: 15px;
		display: inline-block;
		border-radius: 4px;
		background-color: #e8e8e8;
		margin-right: 7px;

		position: relative;

		&:before{
			content:'';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			color: #ea3180;
			font-size: 25px;
    		font-weight: 900;
		}
	}
}

@media screen and (max-width: 1200px){
	.calculate-the-cost{
		&__list{
			flex-direction: column;
		}

		&__item{
			width: 100%;
			margin-right: 0;
			margin-bottom: 20px;

			&:last-child{
				margin-bottom:0;
			}
		}

		&__col{
			padding: 30px 35px;
		}

		&__title{
			margin-bottom: 30px;
		}
	}
}

@media screen and (max-width: 950px){
	.calculate-the-cost{
		&__title{
			font-size: 27px;
		}

		&__col{
			&--another{
				.calculate-the-cost__title.title{
					font-size: 15px;
    				line-height: 20px;
				}
			}
		}
	}
}

@media screen and (max-width: 768px){
	.calculate-the-cost{
		.container{
			flex-direction: column;
		}

		&__col{
			width: 100%;
			padding-bottom: 0;
			margin-bottom: 10px;

			&:last-child{
				margin-bottom:0;
				padding-bottom: 10px;
			}

			&--another{
				.calculate-the-cost{
					&__title.title{
						font-size: 16px;
					    line-height: 20px;
					    margin-bottom: 15px;
					}

					&__row{
						margin-bottom: 14px;
					}

					&__button{
						text-align: center;

						.button--default{
							padding: 8px 0;
						}
					}

					&__label{
						margin-bottom: 5px;
					}
				}
			}
		}

		&__name{
			margin-bottom: 8px;
			font-size: 16px;
		}

		&__item{
			margin-bottom: 10px;
		}

		&__row{
			margin-bottom: 10px;
		}

		&__title{
			margin-bottom: 10px;
			font-size: 23px;
		}

		&__border{
			min-height: 30px;
    		line-height: 30px;
		}

		&__subtitle{
			font-size: 14px;
		}
	}
}

