@media screen and (max-width: 480px){
	.calculate-the-cost{
		.container{
			padding: 0;
		}

		&__col{
			padding: 20px 15px;
		}

		&__button{
			.button--default{
				min-width: initial;
    			padding: 8px 4px;
    			font-size: 15px;
			}
		}
	}
}