.callme{
	display: flex;
	align-items: center;

	&__tel{
		font-weight: 900;
		font-size: 18px;
		color: #fff;
		white-space: nowrap;
	}

	&__button{
		margin-left: 20px;
    	margin-top: -5px;
	}
}

@media screen and (max-width: 700px){
	.callme{
		flex-direction: column;
    	align-items: flex-end;

    	&__tel{
    		margin-bottom: 15px;
    	}
	}
}

@media screen and (max-width: 480px){
	.callme{

    	&__tel{
    		display: none;
    	}

    	&__button{
			margin-left: 0;
			margin-top: 0;
    	}
	}
}