.reviews{
	margin-top: 120px;

	&__title{
		margin-bottom: 118px;
	}
	&__slider{
		.slider{
			&__item{
				/*border: 1px solid black;*/
				padding: 29px 30px;
				box-shadow: 0 0 12px #f2f2f2;
			}

			&__text{
				font-weight: 100;
				font-size: 20px;
				line-height: 30px;
				margin-bottom: 29px;
				font-style: italic;
			}

			&__icon{
				display: inline-block;
				height: 70px;
				width: 70px;
				border: 1px solid transparent;
				border-radius: 50%;
				overflow: hidden;
				margin-bottom: 3px;

				&-pic{
					height: 100%;
					width: 100%;
					object-fit: cover;
					object-position: 50% 15%;
				}
			}

			&__content{
				border-bottom: 1px solid #c5c5c5;
			    padding-bottom: 24px;
			    margin-bottom: 17px;
			}

			&__rating{
				margin-bottom: 31px;
			}
		}
	}
}

@media screen and (max-width: 1060px){
	.reviews{
		&__slider{
			.slider{
				&__wrapper{
					position: relative;
				}

				&__item{
					min-width: 49%;
				}

				&__list{
					overflow:hidden;
				}

				&__controlls{
					position: absolute;
					top: 50%;
					left: 0;
					transform: translateY(-50%);
					height: 40px;
					width: 40px;
					min-width: inherit;

					&--next{
						left: inherit;
						right: 0;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 540px){
	.reviews{
		&__title.title{
			line-height:28px;
		}

		&__slider{
			.slider{
				&__item{
					min-width: 100%;
				}
			}
		}
	}
}