.header{
	min-height: 90px;
	width: 100%;
	background: rgba(#000000, .2);
	display: flex;
	align-items: center;

	.container{
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
	}

	&__col{
		display: flex;
		align-items: center;
	}

	&__logo{
		margin-right:23px;
	}

	&__menu{
		margin-top: -6px;
	}

	&__callme{
		margin-left: 28px;
	}
}

@media screen and (max-width: 1150px){
	.header{
		padding: 10px 0;

		.container{
			flex-direction: column;
		}

		&__logo{
			margin-right: 0;
			margin-bottom: 20px;
		}
	}
}

@media screen and (max-width: 900px){
	.header{

		.container{
			flex-direction: row;
			justify-content: space-between;
		}

		&__menu{
			display: none;
		}

		&__logo{
			margin-bottom: 0;
		}

		&__callme{
			margin-left: 0;

			.callme{
				&__button{
					.button--callme{
						min-width: inherit;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 480px){
	.header{
		&__callme{
			margin-left: 20px;
		}
	}
}