.price-for-work{
	background-color: #f9f9f9;
	padding-top: 130px;

	.container{}

	&__title{
		margin-bottom: 96px;
	}

	&__head{
		&-list{
			display: flex;
			align-items: center;
		}

		&-content{
			background-color: #fff;
			text-align: center;
			margin-right: 4px;
			padding: 21px 0;
			width: 33%;

			&:last-child{
				margin-right:0;
			}

			&--active{
				.content{
					&__title{
						color: #ff0079;
					}
				}
			}
		}
	}

	&__content{
		padding-top: 33px;
    	padding-left: 32px;
	}

	&__list{
		margin-top: 62px;
		display: flex;
	}

	&__item{
		width: 33%;
		margin-right: 30px;
		background-color: #fff;
		padding-bottom: 45px;

		&:last-child{
			margin-right:0;
		}
	}

	&__price{
		display: flex;
		color: #ff0079;
		font-weight: 100;
		font-size: 20px;
		margin-top: 15px;
		margin-left: 33px;

		&-real{
			font-weight: 900;
		}

		&-unreal{
			font-weight: 100;
			text-decoration: line-through;
			margin-right: 17px;
		}
	}

	&__img{
		width: 100%;
		height: 200px;
		position: relative;
		margin-top: 26px;

		&-pic{
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}

	&__popular{
		background-color: #f7a238;
		display: inline-flex;
		align-items: center;
		padding: 5px 12px;
		width: 140px;
		transform: rotate(-3deg) skew(-3deg);

		position: absolute;
		top: 6px;
    	right: -8px;
		z-index: 2;

		&-text{
			font-size: 16px;
			color: #fff;
			text-transform: uppercase;
			font-weight: 900;
		}
	}

	&__text{
		font-size: 16px;
		color: #8d8d8d;
		font-weight: 100;
		line-height: 24px;
		margin-top: 25px;
    	margin-left: 32px;
	}

	&__button{
		margin-top:26px;
		text-align: center;
	}
}

@media screen and (max-width: 1030px){
	.price-for-work{
		&__list{
			flex-wrap: wrap;
			justify-content: center;
		}

		&__item{
			width: 48%;
			margin-bottom: 20px;

			&:nth-child(2){
				margin-right: 0;
			}

			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	
}

@media screen and (max-width: 820px){
	.price-for-work{
		&__item{
			width: 75%;
			margin-right: 0;
		}
	}
}

@media screen and (max-width: 768px){
	.price-for-work{
		&__head{
			&-list{
				flex-wrap: wrap;
				justify-content: center;
				align-items: stretch;
			}

			&-content{
				display: inline-flex;
				justify-content: center;
				align-items: center;
			}

			&-content{
				margin-right: 20px;
				width: 40%;
				margin-bottom: 20px;

				&:nth-child(2){
					margin-right: 0;
				}

				&:last-child{
					margin-bottom: 0;
				}
			}
		}

		&__text{
			margin-left:30px;
			margin-right:30px;
		}
	}
}

@media screen and (max-width: 480px){
	.price-for-work{
		&__head{
			&-list{
				flex-direction: column;
			}

			&-content{
				width: 100%;
			}
		}

		&__item{
			width: 100%;
			margin-right: 0;
		}

		&__button{
			margin: 26px 10px 0;

			.button--default{
				min-width: 100%;
			}
		}

		&__content{
			padding-left: 0;
			text-align: center;
		}

		&__price{
			margin-left: 0;
			justify-content: center;
		}

		&__text{
			text-align: center;
		}
	}
}

@media screen and (max-width: 540px){
	.price-for-work{
		&__title.title{
			line-height: 28px;
		}
	}
}