.how-we-rework{
	margin: 120px 0 100px 0;

	&__title{
		margin-bottom: 111px;
	}

	&__slider{
		.slider{
			&__list{
				width: 100%;
				overflow:hidden;
			}

			&__content{
				margin-left: 67px;
    			margin-right: 39%;

    			.content{
    				&__title{
    					line-height: 30px;
    				}
    			}
			}

			&__item{
				box-shadow: 0 0 12px #f2f2f2;
				padding-bottom: 45px;
			}

			&__avatar{
				position: relative;
				cursor: pointer;

				&:before,&:after{
					content:'';
					position: absolute;
				}

				&:before{
					width: 100%;
					height: 100%;
					background: #000000;
					top: 0;
					left: 0;
					opacity: 0;
				}

				&:after{
					height: 78px;
					width: 78px;
					display: inline-block;
					background: url(../images/lypa.png) center center no-repeat;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
					opacity: 0;
				}

				&:hover{
					&:before{
						opacity:0.4;
					}

					&:after{
						opacity: 1;
					}
				}
			}

			&__description{
				margin-top: 18px;
    			margin-left: 71px;
			}

			&__button{
				margin-top: 32px;

				.button{
					&--callme{
						margin-left: 69px;
					}

					&--benefits{
						margin-left: 44px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1000px){
	.how-we-rework{
		&__slider{
			.slider{
				&__content{
					margin:0 65px;
				}

				&__button{
					display: flex;
					flex-direction: column;
					align-items: center;

					.button--callme{
						margin-left: 0;
						margin-bottom: 10px;
					}

					.button--benefits{
						margin-left: 0;
					}
				}

				&__price{
					white-space: nowrap;
				}

				&__controlls{
					height: 45px;
    				min-width: 45px;
				}
			}
		}
	}
}

@media screen and (max-width: 910px){
	.how-we-rework{
		&__slider{
			.slider{

				&__wrapper{
					position: relative;
				}

				&__item{
					min-width: 100%;
					display: flex;
					flex-direction: column;
					align-items: center;
				}

				&__content{
					margin: 0;
				}

				&__description{
					margin-left: 0;
				}

				&__price{
					margin: 41px 0 0;
				}

				&__controlls{
					position: absolute;
					top: 57%;
					left: 0;
					transform: translateY(-57%);

					&--next{
						right: 0;
						left: inherit;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 540px){
	.how-we-rework{
		&__title.title{
			line-height:28px;
			font-size: 25px;
		}
	}
}