.benefits{
	margin-top: 25%;
	
	&__title{
		margin-bottom: 80px;
	}

	&__list{
		display: flex;
		flex-wrap: wrap;
	}

	&__item{
		margin-right: 40px;
	    margin-bottom: 70px;
	    width: 30%;

		&:nth-child(3n){
			margin-right:0;
		}

		&:last-child{
			margin-right:0;
		}
	}

	&__icon{
		display: inline-block;
		margin-bottom: 25px;
		height: 66px;
		position: relative;

		&-pic{
			max-width: 100%;
			max-height: 100%;
		}

		&-number{
			color: #ea3180;
			font-size: 12px;
			font-weight: 900;

			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
		}
	}

	&__content{
		margin-bottom: 14px;

		.content{
			&__descr{
				margin-top: 16px;
				font-size: 16px;
			}
		}
	}

	&__button{
		margin-top: auto;
	}
}
@media screen and (max-width: 1200px){
	.benefits{
		margin-top: 50%;
	}
}



@media screen and (max-width: 885px){
	.benefits{
		&__list{
			justify-content: center;
		}

		&__item{
			width: 47%;
			margin-right: 40px;

			&:nth-child(3n){
				margin-right: 40px;
			}

			&:nth-child(2n){
				margin-right: 0;
			}
		}
	}
}

@media screen and (max-width: 768px){
	.benefits{
		margin-top: 60%;
	}
}

@media screen and (max-width: 725px){
	.benefits{
		&__item{
			margin-right: 0;
			width: 100%;
			text-align: center;

			&:nth-child(3n){
				margin-right: 0;
			}

			&:last-child{
				margin-bottom:0;
			}
		}
	}
}

@media screen and (max-width: 700px){
	.benefits{
		margin-top: 74%;
	}
}

@media screen and (max-width: 590px){
	.benefits{margin-top: 93%;}  
}

@media screen and (max-width: 590px){
	.benefits{margin-top: 127%;}  
}



@media screen and (max-width: 480px){
