.answer-for-quest{
	margin: 120px 0;


	&__title{
		margin-bottom: 90px;
	}

	&__list{
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		align-content: flex-start;
		align-self: flex-start;
	}

	&__content{
		margin-bottom: 4px;
		width: 48%;
		margin-right: 30px;

		&:nth-child(2n){
			margin-right: 0;
		}

		&--active{
			.content__title:after{
				transform:rotate(180deg);
			}

			.answer-for-quest__info{
				margin-top: 36px;
			    margin-left: 29px;
			    margin-bottom: 28px;
			}
		}

		.content{
			&__title{
				background-color: #f9f9f9;
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 100%;
				padding: 23px 0 23px 30px;

				&:after{
					content:'';
					display: inline-block;
					width: 24px;
					height: 13px;
					margin-right: 23px;
					background: url(../images/arrow-pink.png) center center no-repeat;
				}
			}
		}
	}

	&__info{
		font-size: 16px;
		font-weight: 100;
	}
}

@media screen and (max-width: 830px) {
	.answer-for-quest{
		&__content{
			width: 100%;
			margin-right: 0;

			&:last-child{
				margin-bottom: 0;
			}

			.content{
				&__title{
					font-size: 16px;

					&:after{
						min-width: 36px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 320px){
	.answer-for-quest{
		&__content{
			.content{
				&__title{
					&:after{
						margin-right:10px;
					}
				}
			}
		}
	}
}